import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import TodoList from '../TodoListProject/TodoList'
import Weather from "../WeatherProject/Weather"
import DisplayClocks from '../ClockWorldProject/DisplayClocks';
import PasswordGenerator from '../PasswordGeneratorProject/PasswordGenerator';
import Gallery from '../ImageGaleryProject/Gallery';
import BasicAccordion from './BasicAccordion';

export default function AccordionPresentationPortfolio() {
    return (
        <div>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>Sample App of ToDo List</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <TodoList />




                    {/* */}
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>Sample App of Weather</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Weather />
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>Sample App of World Clocks</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <DisplayClocks />
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>Sample App Password Generator</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <PasswordGenerator />
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>Sample App Gallery</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Gallery />
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>Sample App Accordion</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <BasicAccordion />
                </AccordionDetails>
            </Accordion>
            <Accordion disabled>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel3a-content"
                    id="panel3a-header"
                >
                    <Typography>Disabled Accordion</Typography>
                </AccordionSummary>
            </Accordion>
        </div>
    );
}